<template>
    <div class="board-border p-4" :style="`border-left-color: ${color};`">
        <el-row>
            <el-col :md="10" :sm="24">
                <p>{{ item.title }}</p>
                {{ item.description }}
            </el-col>
            <el-col :md="14" class="hidden-sm-and-down">
                <div v-for="(item, index) in topices" :key="index">
                    <div class="horiz-container m-1">
                        {{ item.title }}
                        <span class="spacer"></span>
                        {{ item.updatedAt | datetimeFormat }}
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import moment from 'moment';

import TopicService from '../../../api/service/topic_service';
import Topic from '../../../model/topic';
export default {
    name: 'Board',
    props: ['item', 'color'],   // item: 外部传入的 版块 值, color: 外部传入的 左则粗边框颜色
    data: () => ({
        topices: [],
        pageSize: 8,
    }),
    mounted() {
        this.getTopices();
    },
    methods: {
        async getTopices() {
            this.topices = [];
            let response = await TopicService.fetchAll(this.pageSize, 0, [], ['-level', '-updatedAt']);
            this.topices = response.map(item => { return new Topic().formJson(item) });
        }
    },

    // 过滤器
    filters: {
        // 使用 moment.js 定义日期时间过滤器函数
        datetimeFormat(val) {
            return moment(val).fromNow();;
        },
    },



}
</script>

<style>
.board-border {
    border-left-style: solid;
    border-left-width: 12px;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-bottom-color: darkgray;
}
</style>